import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { MDBContainer, MDBRow, MDBCol, MDBInput, MDBBtn } from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
import axios from "axios";
const api = {
  result: [
    "09:00",
    "09:10",
    "09:20",
    "09:30",
    "09:40",
    "09:50",
    "10:00",
    "10:10",
    "10:20",
    "10:30",
    "10:40",
    "10:50",
    "11:00",
    "11:10",
    "11:20",
    "11:30",
    "11:40",
    "11:50",
    "12:00",
    "12:10",
    "12:20",
    "12:30",
    "12:40",
    "12:50",
    "13:00",
    "13:10",
    "13:20",
    "13:30",
    "13:40",
    "13:50",
    "14:00",
    "14:10",
    "14:20",
    "14:30",
    "14:40",
    "14:50",
    "15:00",
    "15:10",
    "15:20",
    "15:30",
    "15:40",
    "15:50",
    "16:00",
    "16:10",
    "16:20",
    "16:30",
    "16:40",
    "16:50",
    "17:00",
    "17:10",
    "17:20",
    "17:30",
    "17:40",
    "17:50",
    "18:00",
  ],
  status: "success",
};

export default function Step17(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const formRef = new React.createRef();
  const [time, settime] = useState([]);

  const ampm = time.map((t) => {
    if (t.substring(0, 2) >= 12) {
      if (t.substring(0, 2) == 12)
        return `${parseInt(t.substring(0, 2))}:${t.substring(3, 5)} PM`;
      return `${parseInt(t.substring(0, 2)) - 12}:${t.substring(3, 5)} PM`;
    } else {
      return t + " AM";
    }
  });

  const mindate = () => {
    var today = new Date();
    var dd = today.getDate();
    var mm = today.getMonth() + 1; //January is 0!
    var yyyy = today.getFullYear();
    if (dd < 10) {
      dd = "0" + dd;
    }
    if (mm < 10) {
      mm = "0" + mm;
    }

    return yyyy + "-" + mm + "-" + dd;
  };

  const validate = () => {
    setTouched(true);
    const isValid = formRef.current.checkValidity();

    setErrorMsg(!isValid ? "Please enter date" : "");
  };

  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);
  return (
    <Step
      heading={`Fantastic, the location nearest to you is our location located at  ${state.scheduledAddress} Please select the best date and time below for your test!`}
    >
      <img
        src={BackBtn}
        className="btn-back"
        onClick={() => history.push(prevStep)}
      />
      <MDBContainer>
        <form onSubmit={(e) => e.preventDefault()} ref={formRef}>
          <MDBRow center>
            <MDBCol sm={10} md={8}>
              <div className="my-4" />
              <MDBRow center>
                <MDBCol>
                  <MDBInput
                    label="Date"
                    group
                    type="date"
                    name="scheduleDate"
                    min={mindate()}
                    value={state.scheduleDate}
                    onChange={(e) => {
                      validate();
                      onChangeField(e, e.target.name, e.target.value);
                      console.log(
                        `https://cors-anywhere.herokuapp.com/https://quicmind.com/hg-crm/get-scheduletime?date=${e.target.value}%2013:00:40&location_id=${state.location_id}`
                      );
                      axios
                        .get(
                          `https://cors-anywhere.herokuapp.com/https://quicmind.com/hg-crm/get-scheduletime?date=${e.target.value}%2013:00:40&location_id=${state.location_id}`
                        )
                        .then((res) => {
                          console.log(res);
                          settime(res.data.result);
                        });
                    }}
                    required
                    containerClass="mb-0"
                  />
                </MDBCol>
              </MDBRow>
              {time.length > 0 && (
                <MDBRow>
                  <MDBCol className="mt-3">
                    <select
                      className="browser-default custom-select"
                      label="Date of Birth"
                      group
                      type="date"
                      name="scheduleTime"
                      value={state.scheduleTime}
                      onChange={(e) => {
                        validate();
                        onChangeField(e, e.target.name, e.target.value);
                      }}
                      required
                      containerClass="mb-0"
                    >
                      {ampm.map((time) => (
                        <option>{time}</option>
                      ))}
                    </select>
                  </MDBCol>
                </MDBRow>
              )}
              <MDBRow center className="d-flex flex-column align-items-center">
                {touched && errorMsg.length > 0 && (
                  <div className="text-danger mb-2">{errorMsg}</div>
                )}
                {touched && !errorMsg && (
                  <Link to={nextStep}>
                    <MDBBtn
                      color="pink"
                      rounded
                      type="button"
                      className="z-depth-1a"
                    >
                      Next
                    </MDBBtn>
                  </Link>
                )}
                {(!touched || errorMsg) && (
                  <MDBBtn
                    color={"grey"}
                    disabled
                    rounded
                    type="button"
                    className="z-depth-1a"
                  >
                    Next
                  </MDBBtn>
                )}
              </MDBRow>
            </MDBCol>
          </MDBRow>
        </form>
      </MDBContainer>
    </Step>
  );
}
